import React from 'react';
import { Card} from 'react-bootstrap';

function BasicCard(props) {
    let header = null;
    if (props.header) {
        header = <Card.Header as={props.header}>
        {props.heading}
   </Card.Header>
    }
    return (
        <Card>
            {header}
            <Card.Body>
                <Card.Title>
                    {props.title}
                </Card.Title>
                <Card.Subtitle>
                    {props.subtitle}
                </Card.Subtitle>
                <Card.Text>
                    {props.text.map( (text) => {
                        return <p>{text}</p>
                    })}
                </Card.Text>
            </Card.Body>
        </Card>
    )
}

export default BasicCard;