import React from 'react';
import { Card, Accordion } from 'react-bootstrap';

function ClubCard(props) {
    let key = props.eKey;
    return (
        <Card>
            <Accordion.Toggle as={Card.Header} eventKey={key} >
                {props.title}
                </Accordion.Toggle>
        <Accordion.Collapse eventKey={key}>
            <Card.Body>
                <Card.Text>
                    {props.text}
                </Card.Text>
            </Card.Body>
        </Accordion.Collapse>
        </Card>
    )
}

export default ClubCard;